@font-face {
  font-family: Avenir;
  src: url("../fonts/Avenir/AvenirLTStd-Light.otf");
}

@font-face {
  font-family: AvenirMedium;
  src: url("../fonts/Avenir/AvenirLTStd-Medium.otf");
}

@font-face {
  font-family: AvenirHeavy;
  src: url("../fonts/Avenir/AvenirLTStd-Heavy.otf");
}

#demo-input {
  z-index: 9;
  display: flex;
  position: absolute;

  top: -80px;
  left: calc(50% - 75px);
  width: 150px;
  height: 28px;

  border: 1px solid black;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: transparent;
  box-sizing: border-box;
}

.main {
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
}

.clickable {
  display: flex;
  justify-content: center;
  user-select: none;
  cursor: pointer;
}

.hidden-clickable {
  display: none;
  justify-content: center;
  user-select: none;
  cursor: pointer;
}

.menu-svg {
  width: 13px;
  height: 13px;
  margin: auto 0;
}

.size-select-boxes {
  display: flex;
  flex-direction: row;
  align-items: left;
  justify-content: left;
}

.size-select-boxes>div{
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;

  width: 48px;
  height: 48px;
  border: 1px solid #949494;
  margin: 0 12px 0 12px;
  padding: 0 0 0 0;

  user-select: none;
  cursor: pointer;
}

.showClickableOnHover:hover>div {
  display: flex !important;
}

.out-of-stock {
  position: absolute;
  width: 68px;
  margin-top: -48px;
  margin-left: 20px;
  border-bottom: 1px solid #949494;
  transform: rotate(45deg);
  transform-origin: top left;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  background-color: rgb(202, 202, 202);
  outline: 1px solid rgb(160, 160, 160);
}
